raul-filter-bar {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 0.875rem;
}

raul-filter-bar raul-button {
  display: inline-block;
  margin-left: 1.5rem;
}

raul-filter-bar raul-filter-menu {
  margin-left: 1.5rem;
  display: none;
}

@media (min-width: 768px) {
  raul-filter-bar raul-filter-menu {
    display: inline-block;
  }
}